<template>
  <div class="table-list-page">
    <techo-list ref="list" :title="title" :header="header" :data="data" :isShowCode="isShowCode" :disCodeOption="disCodeOption" :recordCount="recordCount"
      @onAdd="handleAdd" @onPageChange="handlePageChange" @onDelete="handleDelete" @onView="handleView" @onEdit="handleEdit"
      @onTreeNode="handleTreeNode">
      <div slot="header">
        <el-row type="flex" justify="space-around" class="techo-list__header-row">
          <el-col :span="10">
            <span class="techo-list__header-col-txt-4">楼栋长姓名：</span>
            <div class="techo-list__header-col-left-4">
              <el-input placeholder="请输入楼栋长姓名" :maxlength="10" size="small" class="techo-list__header-select-full"
                clearable v-model="queryForm.name"></el-input>
            </div>
          </el-col>
          <el-col :span="9">
          </el-col>
          <el-col :span="5" class="techo-list__header-col-button">
            <el-button size="small" class="techo-list__header-button-reset" @click="handleClear">清空</el-button>
            <el-button size="small" class="techo-list__header-button-query" @click="handleQuery">查询</el-button>
          </el-col>
        </el-row>
      </div>
    </techo-list>
    <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :title="title"></techo-details>
    <vAddDialog ref="vAddDialog" :disOption="disCodeOption"></vAddDialog>
  </div>
</template>

<script>
import service from "./services/zz_service.js";
import vAddDialog from "./building-leader-add.vue";

export default {
  components: {
    vAddDialog
  },
  data() {
    return {
      title: "楼栋长",
      queryForm: {
        districtCode: "",
        name: ""
      },
      header: {
        楼栋名称: "buildingName",
        楼栋长姓名: "name",
        手机号码: "phoneNumber",
        小区名称: "housingName",
        //建筑面积: "coveredArea",
        层数: "pliesCount",
        单元数: "elementsCount",
        楼栋户数: "buildingHouseCount",
        //楼栋人数: "buildingPeopleCount",
        //性别: "dicMap.sex",
        //民族: "dicMap.nation",
        //政治面貌: "dicMap.politicsStatus",
        //出生日期: "birth",
        //学历: "dicMap.education",
        
        //固定电话: "telephoneNumber",
        //所在地: "dicMap.locality",
        //所在地详址: "localityDetail"
      },
      data: [],
      recordCount: 0,
      disCodeOption: [],
      isShowCode: ["6102"], // 数默认展开第一级
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "buildLeader"
    };
  },

  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
      this.queryForm.districtCode = data.code;
    });
  },

  watch: {
    queryForm: {
      handler(newName, oldName) {
        this.handleQuery();
      },
      deep: true
    }
  },

  methods: {
    /** 点击行政区树 返回行政区编码 */
    handleTreeNode(node) {
      this.queryForm.districtCode = node.code;
    },
    /** 初始化行政区网格信息 */
    initGridData(code) {
      let checkCode = code || this.queryForm.districtCode; // 选中的行政区级别
      let userCode = constants.REGION.CODE; // 当前账号的行政区级别

      let XZQ = [];
      switch (checkCode.length) {
        case 4: // 市
          XZQ = [code];
          break;
        case 6: // 区县
          XZQ = [checkCode.slice(0, 4), checkCode];
          break;
        case 9: // 街道
          XZQ = [checkCode.slice(0, 4), checkCode.slice(0, 6), checkCode];
          break;
        case 12: // 社区
          XZQ = [
            checkCode.slice(0, 4),
            checkCode.slice(0, 6),
            checkCode.slice(0, 9),
            checkCode
          ];
          break;
        case 14: // 网格
          XZQ = [
            checkCode.slice(0, 4),
            checkCode.slice(0, 6),
            checkCode.slice(0, 9),
            checkCode.slice(0, 12),
            checkCode
          ];
          break;
        default:
          break;
      }
      switch (userCode.length) {
        case 6:
          XZQ = _.drop(XZQ, 1);
          break;
        case 9:
          XZQ = _.drop(XZQ, 2);
          break;
        case 12:
          XZQ = _.drop(XZQ, 3);
          break;
        case 14:
          XZQ = _.drop(XZQ, 4);
          break;
        default:
          break;
      }
      return XZQ;
    },
    /** 添加楼栋长 */
    handleAdd() {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = true;
      this.$refs["vAddDialog"].title = "添加" + this.title;
      this.$refs["vAddDialog"].buildForm.XZQ = this.initGridData();
    },
    /** 删除楼栋长 */
    handleDelete(tableSelection) {
      let that = this;
      this.$popup.showLoading("正在删除中，请稍候...");
      new Promise((resolve, reject) => {
        let count = 0;
        _.map(tableSelection, (item, index) => {
          service.requestDeleteBuildLeader(item.id, data => {
            count++;
            count == tableSelection.length && resolve();
          });
        });
      })
        .then(() => {
          that.$popup.showNotification({
            type: "success",
            message: "楼栋长删除成功！"
          });
          that.handleQuery();
          that.$popup.closeLoading();
        })
        .catch(() => {
          that.$popup.showNotification({
            type: "warning",
            message: "楼栋长删除失败！"
          });
          that.$popup.closeLoading();
        });
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.data = [];
      this.handleQuery();
    },
    /** 查看信息 */
    handleView(index, row) {
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = row;
        this.$refs["details"].visible = true;
      });
    },
    /** 修改信息 */
    handleEdit(index, row) {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = false;
      this.$refs["vAddDialog"].title = "修改" + this.title;
      this.$refs["vAddDialog"].buildForm = {
        XZQ: this.initGridData(row.districtCode),
        buildingId: Number(row.buildingId),
        ..._.pick(_.cloneDeep(row), [
          "elementsCount",
          "pliesCount",
          "localityDetail"
        ])
      };
      this.$refs["vAddDialog"].dialogForm = _.cloneDeep(row);
    },
    /** 清空查询条件 */
    handleClear() {
      this.queryForm = {
        districtCode: constants.REGION.CODE,
        name: ""
      };
    },
    /** 查询列表 */
    handleQuery() {
      let pageInfo = this.$refs["list"].getPageInfo();
      pageInfo = { ...pageInfo, ..._.cloneDeep(this.queryForm) };
      service.requestPageBuildLeader(pageInfo, data => {
        if (data.totalCount != 0 && data.list.length == 0) {
          pageInfo.page = data.totalPage;
          service.requestPageBuildLeader(pageInfo, data => {
            this.recordCount = data.totalCount;
            this.data = data.list;
          });
        } else {
          this.recordCount = data.totalCount;
          this.data = data.list;
        }
      });
    }
  }
};
</script>
