<template>
    <el-dialog class="add-team-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm">
        <el-tabs v-model="activeName">
            <el-tab-pane label="楼栋信息" name="first">
                <el-form ref="buildForm" :model="buildForm" :rules="buildRules" label-width="100px">
                    <el-row :gutter="15">
                        <el-col :span="24">
                            <el-form-item label="行政区划" prop="XZQ">
                                <el-cascader placeholder="请选择行政区网格" :options="disOption" filterable clearable change-on-select 
                                    v-model="buildForm.XZQ" :props="{value: 'code' }"></el-cascader>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="楼栋名称" prop="buildingId">
                                <el-select placeholder="请选择楼栋" v-model="buildForm.buildingId" clearable @change="handleBuildByHouse">
                                    <el-option v-for="(item, index) in buildOptions" :label="item.name" :value="item.id" :key="index"></el-option>
                                </el-select>
                            </el-form-item>  
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="单元数">
                                <el-input v-model="buildForm.elementsCount" disabled :maxlength="3" placeholder="请填写单元数"></el-input>
                            </el-form-item> 
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="层数">
                                <el-input v-model="buildForm.pliesCount" disabled :maxlength="3" placeholder="请填写层数"></el-input>
                            </el-form-item> 
                        </el-col>
                    </el-row>
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="所在地">
                                <el-select placeholder="请选择居住地" clearable disabled v-model="buildForm.locality">
                                    <el-option v-for="(item, index) in dic['行政区划']" :label="item.name" :value="item.code" :key="index"></el-option>                       
                                </el-select>
                            </el-form-item> 
                        </el-col>
                        <el-col :span="16">
                            <el-form-item label="所在地详址">
                                <el-input v-model="buildForm.localityDetail" placeholder="请填写所在地详址"></el-input>
                            </el-form-item> 
                        </el-col>
                    </el-row>
                </el-form>
            </el-tab-pane>
            <el-tab-pane label="楼栋长" name="second">        
                <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="100px">
                    <el-row :gutter="15">
                        <el-col :span="8">
                            <el-form-item label="小区名称" prop="housingName">
                                <el-input v-model="dialogForm.housingName" placeholder="请填写小区名称"></el-input>
                            </el-form-item>
                            <el-form-item label="建筑面积" prop="coveredArea">
                                <el-input v-model="dialogForm.coveredArea" :maxlength="8" placeholder="请填写建筑面积"></el-input>
                            </el-form-item>  
                            <el-form-item label="性别">
                                <el-select placeholder="请选择性别" clearable v-model="dialogForm.sex">
                                    <el-option v-for="(item, index) in dic['性别']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>     
                            <el-form-item label="学历">
                                <el-select placeholder="请选择机构" clearable v-model="dialogForm.education">
                                    <el-option v-for="(item, index) in dic['学历']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="楼栋长姓名" prop="name">
                                <el-input v-model="dialogForm.name" placeholder="请填写楼栋长姓名"></el-input>
                            </el-form-item> 
                            <el-form-item label="楼栋户数" prop="buildingHouseCount">
                                <el-input v-model="dialogForm.buildingHouseCount" :maxlength="4" placeholder="请填写楼栋户数"></el-input>
                            </el-form-item>  
                            <el-form-item label="民族">
                                <el-select placeholder="请选择性别" clearable v-model="dialogForm.nation">
                                    <el-option v-for="(item, index) in dic['民族']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>  
                            <el-form-item label="出生日期">
                                <el-date-picker v-model="dialogForm.birth" type="date" placeholder="请选择日期"></el-date-picker>
                            </el-form-item>  
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="手机号码" prop="phoneNumber">
                                <el-input v-model="dialogForm.phoneNumber" placeholder="请填写手机号码"></el-input>
                            </el-form-item>  
                            <el-form-item label="楼栋人数" prop="buildingPeopleCount">
                                <el-input v-model="dialogForm.buildingPeopleCount" :maxlength="5" placeholder="请填写楼栋人数"></el-input>
                            </el-form-item> 
                            <el-form-item label="政治面貌">
                                <el-select placeholder="请选择政治面貌" clearable v-model="dialogForm.politicsStatus">
                                    <el-option v-for="(item, index) in dic['政治面貌']" :label="item.name" :value="item.code" :key="index"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="固定电话" prop="telephoneNumber">
                                <el-input v-model="dialogForm.telephoneNumber" placeholder="请填写固定电话"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </el-tab-pane>
        </el-tabs>
        <div slot="footer" class="dialog-footer">
            <el-button v-if="activeName == 'first'" type="primary" size="small" @click="activeName = 'second'">下一步</el-button>
            <el-button v-if="activeName == 'second'" type="primary" size="small" @click="activeName = 'first'">上一步</el-button>
			<el-button v-if="activeName == 'second'" type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button v-if="activeName == 'second'" type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/zz_service.js";
export default {
  props: {
    disOption: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      activeName: "first",
      dic: {
        性别: [],
        民族: [],
        政治面貌: [],
        学历: [],
        行政区划: []
      },
      buildForm: {
        XZQ: [],
        buildingId: "",
        elementsCount: "",
        pliesCount: "",
        localityDetail: "",
        locality: "610000"
      },
      buildRules: {
        XZQ: [
          { required: true, message: "请选择行政区网格", trigger: "change" }
        ],
        buildingId: [
          { required: true, message: "请选择楼栋", trigger: "change" }
        ],
      },
      buildOptions: [],
      dialogForm: {
        sex: "1", // 男性
        politicsStatus: "13", // 群众
        nation: "01", // 汉族
        education: "20", // 大学本科
      },
      teamRules: {
        housingName: [
          { required: true, message: "请输入小区名称", trigger: "blur" },
          { min: 2, max: 25, message: "长度在 2 到 25 个字符", trigger: "blur" }
        ],
        name: [
          { required: true, message: "请输入楼栋长姓名", trigger: "blur" },
          { min: 2, max: 12, message: "长度在 2 到 12 个字符", trigger: "blur" }
        ],
        phoneNumber: [
          { required: true, message: "请输入手机号码", trigger: "blur" },
          util.validate.valiTellPhone()
        ],
        coveredArea: [util.validate.valiInteger()],
        buildingHouseCount: [util.validate.valiInteger()],
        buildingPeopleCount: [util.validate.valiInteger()],
        telephoneNumber: [util.validate.tellPhone()]
      }
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },

  watch: {
    activeName(name) {
      if (name == "second") {
        this.$refs["buildForm"].validate(val => {
          if (!val) {
            this.$nextTick(() => {
              this.activeName = "first";
            });
          }
        });
      }
    },
    "buildForm.XZQ"(value) {
      this.buildOptions = [];
      if (value.length != 0 && value[0] != undefined) {
        // 查询楼栋列表
        service.requestCodeByBuild(_.last(value), data => {
            this.buildOptions = data.list;
        });
      }
      this.buildForm.buildingId = "";
      this.buildForm.elementsCount = "";
      this.buildForm.pliesCount = "";
      this.buildForm.buildingName = "";
      this.buildForm.localityDetail = "";
    },
  },

  methods: {
    /** 选中楼栋 */
    handleBuildByHouse(buildId) {
      let buildInfo = _.find(this.buildOptions, item => {
        return item.id == buildId;
      });
      this.buildForm.elementsCount = buildInfo.unitCount;
      this.buildForm.pliesCount = buildInfo.floorCount;
      this.buildForm.buildingName = buildInfo.name;
      this.buildForm.districtCode = buildInfo.districtCode;
      // 根据编号查询详细地址
      service.requestCodeByAddress(buildInfo.districtCode, data => {
        this.buildForm.localityDetail = (data.belongname || "") + data.name + buildInfo.name;
      });
    },
    /** 保存 */
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let param = {..._.cloneDeep(this.dialogForm), ..._.cloneDeep(this.buildForm)};
          param.birth = param.birth && util.time.formatDate(param.birth, "YYYY-MM-DD");
          delete param.XZQ;
          param.dicMap && delete param.dicMap;
          if (this.isAdd == true) {
            // 添加
            service.requestSaveBuildLeader(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "楼栋长添加成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          } else if (this.isAdd == false) {
            // 修改
            service.requestUpdateBuildLeader(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "楼栋长修改成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    /** 取消 */
    resetForm() {
      this.$refs["buildForm"].resetFields();
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.activeName = "first";
      this.buildForm = {
        XZQ: [],
        buildingId: "",
        elementsCount: "",
        pliesCount: "",
        localityDetail: "",
        locality: "610000"
      };
      this.dialogForm = {
        sex: "1", // 男性
        politicsStatus: "13", // 群众
        nation: "01", // 汉族
        education: "20", // 大学本科
      };
    }
  }
};
</script>
<style lang="scss">
.add-team-dialog {
  .el-date-editor.el-input {
    width: 100%;
  }
  .el-select,
  .el-cascader {
    width: 100%;
  }
  .dialog-footer {
    text-align: center;
  }
}
</style>